body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}

body::-webkit-scrollbar {
  background-color: #f0f0f0;
  background-color: var(--white240);
}

body::-webkit-scrollbar-thumb {
  border-radius: 0.2rem;
  background-color: #c1e9af;
  background-color: var(--primary_light);
  box-shadow: inset 0.1rem 0.05rem 0.2rem #c8c8c8;
  box-shadow: inset 0.1rem 0.05rem 0.2rem var(--grey200);
}

body::-webkit-scrollbar-thumb:active {
  background-color: #71995f;
  background-color: var(--primary);
}

:root {
  font-size: 16px;
  scroll-behavior: smooth;
}

@media screen and (max-width: 840px) {
  :root {
    font-size: 14px;
  }
}

@media screen and (max-width: 420px) {
  :root {
    font-size: 12px;
  }
}

:root {
  --white250: #fafafa;
  --white245: #f5f5f5;
  --white240: #f0f0f0;

  --black20: #141414;
  --black40: #282828;
  --black60: #3c3c3c;

  --grey120: #787878;
  --grey160: #a0a0a0;
  --grey200: #c8c8c8;

  --red1: #af0000;
  --red2: #d77878;

  --primary: #71995f;
  --primary_dark: #497137;
  --primary_light: #c1e9af;

  --secondary: #6c3c2c;
  --secondary_light: #ecbcac;
  --secondary_dark: #4c1c0c;

  --white250_p8_opacity: rgba(250, 250, 250, 0.8);
  --grey200_p5_opacity: rgba(200, 200, 200, 0.5);
  --primary_light_p8_opacity: rgba(193, 233, 175, 0.8);
}

:root {
  --text1: var(--black20);
  --text2: var(--black40);
  --text3: var(--black60);
  --text4: var(--white250);

  --surface1: var(--white250);
  --surface2: var(--white245);
  --surface3: var(--white240);
  --surface4: var(--primary_light);

  --shadow1: var(--white240);
  --shadow2: var(--grey200);
  --shadow3: var(--grey120);

  --border1: var(--grey120);
  --border2: var(--grey160);
  --border3: var(--grey200);

  --button1: var(--primary);
  --button2: var(--primary_dark);
  --button3: var(--primary_light);

  --link: var(--primary);

  --error1: var(--red1);
  --error2: var(--red2);
}
